.cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
    grid-gap: 20px;
    flex-wrap: wrap;
    background-color: white;
}

.card {
    display: grid;
    grid-template-rows: max-content 200px 1fr;
    border: 2px solid salmon;
    padding: 5px;
    margin: 10px;
    border-radius: 10px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

span{
    font-size: smaller;
    font-style: italic;

}

.card img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    margin-top: 10px;
}
    
p {
    
    text-align: justify;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 8;
    overflow: hidden;
  }

  input{
    width: 300px;
  }

  button{
    border: 1px solid salmon;
    border-radius: 5px;
  }

  button:hover{
    background-color: rgb(245, 159, 159);
    transition: 0.7s;
    font-style: italic;
    cursor: pointer;
    
  }